t-family: \"Europa\";\n}\n\nbody {\n  position: relative;\n  padding: 1.5rem 1.5rem 0;\n  padding: 2vmax 2vmax 0;\n  color: $black;\n  overflow-x: hidden;\n  height: 100%;\n  overflow: visible;\n  &.about-page-opened {\n    overflow: hidden;\n  }\n}\n#app {\n  position: relative;\n  min-height: 100vh;\n  min-height: calc(100vh - 1.5rem);\n  min-height: calc(100vh - 2vmax);\n  width: 100%;\n  background: $offwhite;\n  display: flex;\n  overflow: hidden;\n  @include tall {\n    flex-direction: column;\n    padding: 0 3vmax;\n  }\n  @include wide {\n    flex-direction: row;\n  }\n  &.invert-colors {\n    background: $soft-black;\n  }\n  z-index: 1;\n  > * {\n    //z-index: 1;\n  }\n  .cover-image-wrap {\n    z-index: -1;\n  }\n}\n\n.page {\n  min-height: 100%;\n  @include tall {\n    position: relative;\n    flex: 1;\n    .pagewrap {\n      margin-top: 3vh;\n    }\n  }\n  @include wide {\n    width: 55vw;\n    padding: 20vh 4vw 10vh 4.75vw;\n    &.page-work {\n      padding: 20vh 4.5vw 10vh 3.25vw;\n    }\n    margin-left: auto;\n  }\n}\n","$font_path: \"../assets/fonts/\";\n$base-font: 1em;\n$black: #18171d;\n$pink: #f58092;\n$blue: #353544;\n$red: #ce374f;\n$yellow: #f7b267;\n$offwhite: #f5f5f5;\n$soft-black: #201f1f; //dark theme\n@mixin wide {\n  @media (orientation: landscape) {\n    @content;\n  }\n}\n\n@mixin tall {\n  @media (orientation: portrait) {\n    @content;\n  }\n}\n\n@mixin stroke_and_fill($color) {\n  fill: $color;\n  stroke: $color;\n}\n","h1,\nh2,\nh3,\nh4,\nh5 {\n  font-weight: bold;\n}\n\nh2 {\n  color: $blue;\n}\n\np,\na {\n  line-height: 1.5;\n  @include wide {\n    font-size: 3vw;\n    @media (min-width: 1024px) {\n      font-size: 2vw;\n    }\n  }\n  @include tall {\n    font-size: 5.5vw;\n    line-height: 1;\n    margin-bottom: 1.5vh;\n  }\n}\n\na {\n  color: $red;\n  text-decoration: none;\n  .text-links &,\n  &.text-link {\n    p & {\n      margin: 0;\n    }\n    position: relative;\n    &:before {\n      position: absolute;\n      top: 0;\