ansform: translate3d(0, -1rem, 0);\n  }\n  to {\n    opacity: 1;\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@keyframes fadeInAlt {\n  from {\n    opacity: 0;\n    transform: translate3d(0, -0.33rem, 0);\n  }\n  to {\n    opacity: 1;\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@keyframes fadeInFromLeft {\n  from {\n    opacity: 0;\n    transform: translate3d(-2rem, 0, 0);\n  }\n  to {\n    opacity: 1;\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@keyframes revealPage {\n  0% {\n    border-radius: 100%;\n    width: 0;\n    height: 0;\n  }\n  100% {\n    border-radius: 100%;\n    width: 100vw;\n    height: 100vw;\n  }\n}\n\n@keyframes bodyappear {\n  from {\n    opacity: 0;\n    transform: translate3d(-0.5rem, 0, 0);\n  }\n  to {\n    opacity: 1;\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n.body-appear {\n  animation: bodyappear 0.33s forwards;\n}\n\n@keyframes fromBottomCircle {\n  from {\n    transform: translate3d(0, 100%, 0);\n    opacity: 0;\n  }\n  to {\n    transform: translate3d(0, 0, 0);\n    opacity: 1;\n  }\n}\n\n@keyframes float {\n  0% {\n    transform: translate3d(0, 0, 0);\n  }\n  50% {\n    transform: translate3d(0, -1.5vmax, 0);\n    @media (max-width: 767px) {\n      transform: translate3d(0, -6vmax, 0);\n    }\n  }\n  100% {\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@keyframes floatSmall {\n  0% {\n    transform: translate3d(0, 0, 0);\n  }\n  50% {\n    transform: translate3d(0, 4vmax, 0);\n    @media (max-width: 767px) {\n      transform: translate3d(0, 12vmax, 0);\n    }\n  }\n  100% {\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@keyframes floatMedium {\n  0% {\n    transform: translate3d(0, 0, 0);\n  }\n  50% {\n    transform: translate3d(0, -4vmax, 0);\n    @media (max-width: 767px) {\n      transform: translate3d(0, -12vmax, 0);\n    }\n  }\n  100% {\n    transform: translate3d(0, 0, 0);\n  }\n}\n\n@keyframes floatLarge {\n  0% {\n    transform: translate3d(0, 0, 0);\n  }\n  50% {\n    transform: translate3d(-2vma